import React, { useState } from 'react';

// List of words to find in the word maze
const wordsToFind = ["Pizza", "Burger", "Pasta", "Salad", "Sushi", "Tacos", "Stew", "Biryani"]; // Example words

// Function to generate the matrix
const generateMatrix = (size) => {
  const letters = "ABCDEFGHIJKLMNOPQRSTUVWXYZ";
  let matrix = Array(size).fill(null).map(() => Array(size).fill(''));
  let wordPositions = [];

  // Place words in the matrix
  wordsToFind.forEach(word => {
    let placed = false;
    while (!placed) {
      const direction = Math.floor(Math.random() * 8); // 8 directions (horizontal, vertical, diagonal)
      const row = Math.floor(Math.random() * size);
      const col = Math.floor(Math.random() * size);

      if (canPlaceWord(matrix, word, row, col, direction, size)) {
        placeWord(matrix, word, row, col, direction);
        wordPositions.push({ word, row, col, direction });
        placed = true;
      }
    }
  });

  // Fill remaining empty spots with random letters
  for (let i = 0; i < size; i++) {
    for (let j = 0; j < size; j++) {
      if (!matrix[i][j]) {
        matrix[i][j] = letters.charAt(Math.floor(Math.random() * letters.length));
      }
    }
  }

  return { matrix, wordPositions };
};

// Check if the word can be placed in the given direction
const canPlaceWord = (matrix, word, row, col, direction, size) => {
  const directions = [
    [-1, 0], [1, 0], [0, -1], [0, 1], // vertical, horizontal
    [-1, -1], [-1, 1], [1, -1], [1, 1] // diagonals
  ];

  const [dx, dy] = directions[direction];
  for (let i = 0; i < word.length; i++) {
    const newRow = row + dx * i;
    const newCol = col + dy * i;
    if (newRow < 0 || newRow >= size || newCol < 0 || newCol >= size || matrix[newRow][newCol] !== '') {
      return false;
    }
  }
  return true;
};

// Place the word in the matrix
const placeWord = (matrix, word, row, col, direction) => {
  const directions = [
    [-1, 0], [1, 0], [0, -1], [0, 1], // vertical, horizontal
    [-1, -1], [-1, 1], [1, -1], [1, 1] // diagonals
  ];

  const [dx, dy] = directions[direction];
  for (let i = 0; i < word.length; i++) {
    matrix[row + dx * i][col + dy * i] = word[i];
  }
};

const PuzzleGame = () => {
  const [gridSize, setGridSize] = useState(10); // Default to 10x10 grid
  const [matrixData, setMatrixData] = useState(generateMatrix(gridSize));
  const [selectedWord, setSelectedWord] = useState([]);
  const [score, setScore] = useState(0);
  const [message, setMessage] = useState('');
  const [lockedWords, setLockedWords] = useState([]); // Track locked words
  const [highlightedCells, setHighlightedCells] = useState([]); // Track highlighted cells for all words

  // Handle cell click event
  const handleCellClick = (rowIndex, colIndex) => {
    // Don't allow selection of already highlighted (locked) cells
    if (highlightedCells.some(cell => cell.rowIndex === rowIndex && cell.colIndex === colIndex)) {
      return;
    }

    const cellLetter = matrixData.matrix[rowIndex][colIndex];
    const newSelection = [...selectedWord, { letter: cellLetter, rowIndex, colIndex }];
    setSelectedWord(newSelection);
  };

  // Handle word verification
  const handleVerify = () => {
    const word = selectedWord.map(cell => cell.letter).join('');
    if (wordsToFind.includes(word) && !lockedWords.includes(word)) {
      // Mark the word as locked
      setLockedWords([...lockedWords, word]);

      // Highlight the word's cells with red border
      const wordCells = selectedWord.map(cell => ({ rowIndex: cell.rowIndex, colIndex: cell.colIndex }));
      setHighlightedCells(prevHighlightedCells => [
        ...prevHighlightedCells,
        ...wordCells // Append this word's cells to the existing highlighted ones
      ]);

      // Update score and message
      setScore(score + 1);
      setMessage('Correct! +1 score.');
    } else {
      setMessage('Incorrect or already selected word. Try again.');
    }

    // Clear the selected word
    setSelectedWord([]);
  };

  // Shuffle the matrix
  const handleShuffle = () => {
    setMatrixData(generateMatrix(gridSize));
    setSelectedWord([]);
    setMessage('');
    setLockedWords([]); // Reset locked words
    setHighlightedCells([]); // Reset highlighted cells
  };

  // Reset the game
  const handleReset = () => {
    setSelectedWord([]);
    setScore(0);
    setMessage('');
    setMatrixData(generateMatrix(gridSize));
    setLockedWords([]); // Reset locked words
    setHighlightedCells([]); // Reset highlighted cells
  };

  // Change difficulty (size of the grid)
  const handleDifficultyChange = (newSize) => {
    setGridSize(newSize);
    setMatrixData(generateMatrix(newSize));
    setSelectedWord([]);
    setScore(0);
    setMessage('');
    setLockedWords([]); // Reset locked words
    setHighlightedCells([]); // Reset highlighted cells
  };

  return (
    <div className="puzzle-game">
      <h1>Word Maze Game</h1>
      <h2>Score: {score}</h2>
      <div className="difficulty-controls">
        <button onClick={() => handleDifficultyChange(10)}>Easy (10x10)</button>
        <button onClick={() => handleDifficultyChange(12)}>Medium (12x12)</button>
        <button onClick={() => handleDifficultyChange(15)}>Hard (15x15)</button>
      </div>
      <div className="message">{message}</div>
      <div className="matrix">
        {matrixData.matrix.map((row, rowIndex) => (
          <div key={rowIndex} className="matrix-row">
            {row.map((cell, colIndex) => {
              const isSelected = selectedWord.some(item => item.rowIndex === rowIndex && item.colIndex === colIndex);
              const isHighlighted = highlightedCells.some(cell => cell.rowIndex === rowIndex && cell.colIndex === colIndex);
              
              return (
                <div
                  key={colIndex}
                  className={`matrix-cell ${isSelected ? 'selected' : ''} ${isHighlighted ? 'highlighted' : ''}`}
                  onClick={() => handleCellClick(rowIndex, colIndex)}
                >
                  {cell}
                </div>
              );
            })}
          </div>
        ))}
      </div>
      <div className="controls">
        <button onClick={handleVerify} disabled={selectedWord.length === 0}>Verify</button>
        <button onClick={handleShuffle}>Shuffle Matrix</button>
        <button onClick={handleReset}>Reset Game</button>
      </div>
    </div>
  );
};

export default PuzzleGame;
